import AutosizeInput from 'react-input-autosize';
import { useState, useEffect } from "react";
import { VscError } from "react-icons/vsc";
import { IoIosCheckmarkCircle } from "react-icons/io";

const UserInput = ({word, triggerNext, helped}) => {
  const [userInput, setUserInput] = useState("");
  const [isSolved, setIsSolved] = useState(false)
  let sentence = word.sentence
  
  function splitSentence(s,i){
    let arr = [];
    arr[0] = s.substr(0,i);
    arr[1] = s.substr(i);
    return arr
  }


  const solvedIconStyle = {
    backgroundColor: "white",
    color: "#457fca", 
    margin: "0rem",
    borderRadius: "100%",
    fontSize: "1.2rem"
  }
  const notSolvedIconStyle = {
    backgroundColor: "white",
    color: "#ff5e00", 
    margin: "0rem",
    borderRadius: "100%",
    fontSize: "1.2rem"
  }


  useEffect(()=>{
    if(userInput===word.word){
      
      setIsSolved(true);
      setTimeout(() => {
          
        setIsSolved(false);
        setUserInput('');
        triggerNext();
      }, 1500);

    }

    return ()=>{
      console.log("USE Effect cleanup")
    }

  },[userInput])

  function handleUserInput (e){
    setUserInput(e.target.value);
  }
  
  function submitFunction(e){e.preventDefault()}

  return (
    <>
    <form onSubmit={submitFunction}  autoComplete="off">
    <div className="feedbackBox">
      {isSolved && <IoIosCheckmarkCircle style={solvedIconStyle}/>}
      {!isSolved && <VscError style={notSolvedIconStyle}/>}
    </div>
    <span>{splitSentence(sentence,word.wordIndex)[0]}</span>
    {helped && <AutosizeInput autoFocus className="userAnswerInput"
	name="form-field-name"
	value={word.word}
  onChange={()=>{
  }}
/>}
    {!helped && <AutosizeInput autoFocus className="userAnswerInput"
	name="form-field-name"
	value={userInput}
  onChange={handleUserInput}
/>}
   
      <span className="form-second-span">{splitSentence(sentence,word.wordIndex)[1]}</span> 

    
    
    </form>

    </>
  );
}
 
export default UserInput;
